.shop-nav {
	display: flex;
	align-items: center;

	&__item {
		&:not(:last-child) {
			margin-right: 25px;
		}
	}

	
	
	&__link {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;

		&--user {
			background-image: url("../img/user.png");
		}
	}
}

.cart {
	width: auto;
	height: auto;

	&__icon {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		background-image: url("../img/cart.png");
	}

	&__quantity {
		display: inline-flex;
		align-items: center;
		border-radius: 25px;
		font-size: 10px;
		font-weight: 700;
		padding: 0 5px;
		height: 16px;
		color: var(--color-white);
		background-color: var(--color-dark);

	}
}

.search-form {
	&__btn {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-image: url("../img/search.png");
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}
}