.main-products {
  &__container {
    padding-top: 55px;
    padding-bottom: 55px;
  }

  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 46px;
  }

  &__item {
    margin: 0 18px;
  }

  &__btn {
    font-size: 24px;

    &--current {
      &::before {
        width: 100%;
        opacity: 1;
      }
    }
  }

  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    color: var(--color-gray);
    font-size: 13px;
    font-weight: 500;
    margin-top: 95px;

    span {
      display: inline-block;
      margin-bottom: 12px;
    }

    svg {
      width: 12px;
      height: 21px;
      fill: var(--color-dark);
    }
  }

  &__btn-center {
    text-align: center;
  }
}
