.banners {
	&__container {
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__list {
		display: flex;
	}

	&__item {
		flex-shrink: 0;
		width: 33%;

		&:not(:last-child) {
			margin-right: 0.6%;
		}
	}
}

.banners-article {
	padding: 54px 60px;
	padding-right: 45%;
	min-height: 290px;
	background-color: #eeeeec;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;

	&--midseason {
		background-image: url("../img/midseason.jpg");
	}

	&--summer {
		background-image: url("../img/summer.jpg");
	}

	&--accs {
		background-image: url("../img/accs.jpg");
	}

	&__title {
		margin: 0;
		margin-bottom: 20px;
		font-size: 36px;
	}
}