.product {
  position: relative;

  &__image {
    margin-bottom: 20px;

    img {
      object-fit: cover;
      max-height: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 13px;
    color: var(--color-tundora);

    a {
      &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }
    }
  }

  &__price {
    font-size: 13px;
    font-weight: 700;
  }
}

.products-grid {
  display: flex;
  flex-wrap: wrap;

  &__item {
    margin-right: 30px;
    margin-bottom: 80px;

    &:nth-child(5n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 5) {
      margin-bottom: 0;
    }
  }
}
