.footer {
  &__content {
    padding: 0 250px;
    padding-top: 98px;
    padding-bottom: 0;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding-bottom: 95px;
  }

  &__column {
    &:nth-child(2) {
      margin-left: -60px;
    }
  }

  &__list {
    &--row {
      display: flex;
      align-items: center;

      & .footer__item {
        &:not(:last-child) {
          margin-right: 42px;
          margin-bottom: 0;
        }
      }
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }

  &__link {
    color: var(--color-tundora);
    font-size: 13px;

    &--row {
      color: var(--color-dark);
      font-weight: 700;
    }
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    margin: 0;
    margin-bottom: 37px;
    text-transform: uppercase;
  }

  &__form {
    transform: translateY(3px);
    width: 646px;
    margin-bottom: 25px;
  }

  &__text {
    color: var(--color-tundora);
    font-weight: 500;
    font-size: 13px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #eeeeee;
    padding: 40px 0;
  }

  &__social {
    margin-right: 12px;
  }
}

.subscribe-form {
  position: relative;

  &__title {
    margin: 0;
    margin-bottom: 38px;
    font-size: 13px;
    font-weight: 500;
    color: var(--color-tundora);
  }

  &__btn {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;
    font-size: 13px;
    font-weight: 500;
    padding: 13px 10px;
    color: var(--color-dark);
  }

  &__field {
    display: block;
    width: 100%;
    border-bottom: 2px solid var(--color-dark);
    padding-bottom: 10px;

    input {
      width: 100%;
      border: none;
      font-size: 13px;
      font-weight: 500;
      color: var(--color-dark);
      padding-right: 8px;

      &::placeholder {
        color: rgba(#454545, 0.5);
      }
    }
  }
}

.social {
  display: flex;
  align-items: center;

  &__item {
    margin-bottom: 0;

    &:not(:last-child) {
      margin-right: 29px;
    }
  }

  &__link {
    display: block;
    width: 16px;
    height: 16px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &--tw {
      background-image: url("../img/tw.png");
    }

    &--fb {
      background-image: url("../img/fb.png");
    }

    &--in {
      background-image: url("../img/in.png");
    }

    &--pin {
      background-image: url("../img/pin.png");
    }
  }
}
