.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--color-quillGray);

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    background-color: rgba(var(--color-black), 0.5);
  }

  &__container {
    position: relative;
    height: 100%;
  }

  &__title {
    max-width: 560px;
    margin: 0;
    margin-bottom: 40px;
    font-size: 72px;
    font-weight: 700;
  }

  &__descr {
    font-size: 18px;
    margin: 0;
  }

  &__image {
    position: absolute;
    right: 273px;
    bottom: 0;
    height: 967px;
    max-height: 90%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 260px;
    height: 100%;
  }
}

.banner-slider {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .swiper-slide {
    height: 100%;
  }
}

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 46px;
}

.swiper-container-horizontal
  > .swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0 12px;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.swiper-container-horizontal
  > .swiper-pagination-bullets
  .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;

  &::before {
    position: absolute;
    content: "";
    left: 50%;
    top: 50%;
    width: 20px;
    height: 20px;
    z-index: -1;
    transform: translate(-50%, -50%);
    background-color: var(--color-white);
    border-radius: 100%;
  }
}

.marketing {
  position: absolute;
  bottom: 50px;
  left: 50px;
  display: flex;
  padding: 10px;
  width: 280px;
  background-color: var(--color-white);
  box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.1);
  z-index: 10;
  transform: translateX(-150vw);
  transition: transform 0.3s;

  &--visible {
    transform: translateX(0);
  }

  &__text {
    margin-left: 10px;
    min-height: 34px;
  }

  &__image {
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__descr {
    display: block;
    font-size: 11px;
    margin-bottom: 5px;
    color: var(--color-gray);
  }

  &__title {
    font-size: 15px;
    margin-bottom: 7px;
    margin: 0;
    margin-bottom: 7px;
    padding-right: 20px;
  }

  &__when-from {
    display: block;
    font-size: 11px;
    padding-right: 40px;
    color: var(--color-gray);
  }

  &__close {
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 0;
    width: 10px;
    height: 10px;
    background-image: url("../img/close.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
